import { configureStore } from "@reduxjs/toolkit";
import productBasketReducer from "@src/pages/ProductsPage/store/reducers/productBasketReducer";
import productReducer from "@src/pages/ProductsPage/store/reducers/productReducer";
import userReducer from "@src/pages/ProfilePage/store/reducer/userReducer";
import notificationReducer from "@src/pages/Container/store/reducer/notificationReducer";
import { apiProduct } from "@src/pages/ProductsPage/store/api/apiProduct";
import { apiFeedback } from "@src/pages/HomePages/store/api";
const mainStore = configureStore({
    reducer: {
        products: productReducer,
        productBasket: productBasketReducer,
        userReducer: userReducer,
        notification: notificationReducer,
        [apiProduct.reducerPath]: apiProduct.reducer,
        [apiFeedback.reducerPath]: apiFeedback.reducer,
    },
    middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(apiProduct.middleware, apiFeedback.middleware),
});
export default mainStore;
